@import url('https://fonts.googleapis.com/css?family=Montserrat')

.App {
  display: block;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.condiv {
  z-index: 3;
  position:  absolute;
  margin: 100px;
  background-color: whitesmoke;
  /* flex:var(--condiv-flex); */
}

.subtopic
{
margin:10px 10px 30px 10px !important;
}


.LiveBackDrop {
  position: absolute;
  background-color: black;
  z-index: 0;
  -webkit-filter: blur(4px);
          filter: blur(4px);
  -webkit-transition: -webkit-filter 0.5s;
  transition: -webkit-filter 0.5s;
  transition: filter 0.5s;
  transition: filter 0.5s, -webkit-filter 0.5s;
  -webkit-transform: scale(1.1);
          transform: scale(1.1); 
  cursor: cell;
}

.UI {
  z-index: 2;  
}

.splashCard {
  display: table-cell;
  height: 60vh;
  width: 46.4vw;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
}

.splashCard img{
  position: absolute;
  width: 50vw;
} 

.construct {
  display: block;
  position: block;
  text-align: center;
}

.linkDiv {
  display: inline-block;
  position: relative;
}

.constructR {
  display: block;
  text-align: center;
  position: absolute;
  padding-top: 8px;
  margin-top: 100px;
  width: 100vw;
  font-size: 2vw;
  height: 8vh;
  background-color: whitesmoke;
}

.UI button {
  text-align: center;
  position: relative;
  color: black;
  
  background: whitesmoke;
  height: 40px;
  width: 90px;
  /* margin-top: 10%; */
  display: inline-block;
  align-items: center;
}

.oldSite {
  text-align: center;
  position: absolute;
  font-family: 'Montserrat';
  color: black;
  border-color: black;
  font-size: 4vh;
  background: white;
  outline: none;
  /* height: 8vh;s */
  width: 10vw;
  height: auto;
  cursor: pointer;
  margin-top: 85vh;
  z-index: 4;
  margin-left: 80vw;
}
