.UI button :hover {
    color: #ff0b0b;
    background: #ff0b0b;
    border: 1px #DADADA solid;
    padding: 5px 10px;
    border-radius: 2px;
    font-weight: bold;
    font-size: 9pt;
  }

  @media only screen and (max-width: 600px) {
    .construct {
      font-size: 10vw;
      display: none;
    }

    .oldSite {
      height: 10vw;
      width: auto;
      margin-left: 60vw;
    }
  }
 

